import {
    Box,
    Text,
    Image,
    Flex,
  } from '@chakra-ui/react';

import bg from '../assets/images/hero_bg.png';

const Hero = () => (
    <Flex align="center">
        <Box w="100vw" h="500px" position="relative" borderRadius="0 0 20px 20px" overflow="hidden">
            <Box
                position="absolute"
                bgColor="rgba(109,182,215,.8)"
                color="white"
                fontSize="20px"
                w="25px"
                textAlign="center"
                py="10px"
                left="5vw"
                top="5vw"
            >
                ようこそ
            </Box>
            <Box
                position="absolute"
                bgColor="rgba(109,182,215,.8)"
                color="white"
                fontSize="20px"
                w="25px"
                textAlign="center"
                py="10px"
                left="15vw"
                top="5vw"
            >
                シロマロです
            </Box>
            <Image w="100vw" h="500px" fit="cover" src={bg}  alt="シロマロ" />
        </Box>
        {/* <Box
            bgImage="url('/static/media/hero_bg.912c6fe9.png')"
            bgPosition="center"
            bgRepeat="no-repeat"
            w="100vw" h="80vh "
        /> */}
    </Flex>
  );
  
  export default Hero;
  