import {
    Text,
    Image,
    Flex,
    Link,
    Grid,
    GridItem,
  } from '@chakra-ui/react';

import phoneCase from '../assets/images/phone_case.png';
import lineSticker from '../assets/images/line_sticker.png';

const Sectionproduct = () => (
    <Grid templateColumns={["repeat(2, 1fr)", "repeat(2, 1fr)","repeat(4, 1fr)", "repeat(4, 1fr)"]} gap={10} my={10} px="1em">
        <GridItem position="relative" W="100%" colSpan={2} bgColor="#B1DDE0" borderRadius="8px">
            <Image w="100%" h="240px" fit="cover" src={phoneCase} alt="シロマロ" />
            <Link href="https://www.instagram.com/shikamaro_painting/">
                <Flex  align="center" justifyContent="center" flexDir="column">
                    <Text fontWeight="bold" fontSize="20px" color="#595959" py="12px">犀牛頓手機殼</Text>
                </Flex>
            </Link>
        </GridItem>
        <GridItem position="relative" W="100%" colSpan={2} bgColor="#D0B08E" borderRadius="8px">
            <Image w="100%" h="240px" fit="cover" src={lineSticker} alt="シロマロ" />
            <Link href="https://store.line.me/stickershop/product/12051614/ja?ref=lsh_stickerDetail">
                <Flex  align="center" justifyContent="center" flexDir="column">
                    <Text fontWeight="bold" fontSize="20px" color="#fff" py="12px">Line 貼圖</Text>
                </Flex>
            </Link>
        </GridItem>
    </Grid>
  );
  
  export default Sectionproduct;
  